<template>
    <grade-box :origin="origin" :title="$t('upgrade')">
        <div class="upgrade">
            <div class="title">
                <span>{{ $t('referralIdentity') }}</span>
                <!-- <p><span>{{ boughts[grade - 1] }}</span>/500</p> -->
            </div>
            <div class="grade">
                <Button :disabled="origin >= 1" :class="grade === 1 ? 'active' : ''" @click="grade = 1">M1</Button>
                <Button :disabled="origin >= 2" :class="grade === 2 ? 'active' : ''" @click="grade = 2">M2</Button>
                <!-- <Button :disabled="origin >= 3" :class="grade === 3 ? 'active' : ''" @click="grade = 3">M3</Button> -->
            </div>
            <div class="text">
                <p>{{ $t('zunxiang') }} <span>M{{ grade }}{{ $t('level') }}</span></p>
                <p>{{ $t('peisong') }} <span>{{ token }} MPC</span></p>
                <p>{{ $t('zhengsong') }} <span v-text="shareLevel"></span></p>
            </div>
            <Button :disabled="origin >= 3" @click="buyGrade" color="linear-gradient(41deg, #F5D755, #FFA271)">{{ $t('liji') }}{{ price }} USDT</Button>
        </div>
        <authorization
		  tokenType="USDT"
		  :show="authorizedAccess" 
		  :click="authorizedAccessFun"
		  :needAuthorization='price' 
		  :authorizationEnds="authorizationEnds"
		  @authorizationEnds="end"
		></authorization>
    </grade-box>
</template>

<script>
    import { Button, Toast } from 'vant';

    export default {
        name: 'grade',
        components: {
            Button,
            'grade-box': () => import(/* WebpackChunkName: 'grade' */ '@/components/grade-box.vue'),
            'authorization': () => import(/* WebpackChunkName: 'grade' */ '@/components/Authorization.vue')
        },
        data() {
            return {
                grade: 0,
                origin: 0,
                v1price: 0,
                v2price: 0,
                v3price: 0,
                usdt: '0',
                authorizedAccess: false,
                authorizationEnds: false,
                USDTAuthorizedAccess: '',
                boughts: []
            }
        },
        computed: {
            shareLevel() {
                switch (this.grade) {
                    case 2:
                        return '2个M1';
                    case 3:
                        return '4个M1+2个M2';
                    case 1:
                    default:
                        return 0;
                }
            },
            token() {
                switch (this.grade) {
                    case 1:
                        return this.v1Token;
                    case 2:
                        return this.v2Token;
                    case 3:
                        return this.v3Token;
                    default:
                        return 0;
                }
            },
            price() {
                switch (this.grade) {
                    case 1:
                        return this.v1price;
                    case 2:
                        return this.v2price;
                    case 3:
                        return this.v3price;
                    default:
                        return 0;
                }
            }
        },
        methods: {
            buyGrade() {
                if(Number(this.usdt) < Number(this.price)){
					return Toast.fail(this.$t('InsufficientBalance'));
				}else if(Number(this.USDTAuthorizedAccess) < Number(this.price)){
					return this.authorizedAccess = true;
                }
                Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});

                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'paymentDLevel(uint256)',
                    parameter: [{ type: 'uint256', value: this.grade }]
				}).then(async result => {

					this.$contracts.Manager.paymentDLevel(this.grade)
                    .send({ feeLimit: '50'.toSun() }).then(hex => {
                        sessionStorage.setItem('transactionHex', hex);

                        const interval = setInterval(() => {
                            window.tronWeb.trx.getTransaction(hex).then(transaction => {
                                if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
                                    sessionStorage.removeItem('transactionHex');
                                    clearInterval(interval);
                                    
                                    Toast.clear();
                                    this.App_Push('/setting');
                                }
                            })
                        }, 2000);

                    }).catch(err => {
                        Toast.clear();
                        console.log(err);
                    });

                }).catch(err => {
					Toast.clear();
					console.log(err);
				});
            },
            authorizedAccessFun() {
                Toast.loading({
					message: '正在交易中...',
					forbidClick: true,
					duration: 0
				});
                
				this.$contracts.ERC20Token.approve(
					this.$contracts.Manager.address,
					this.price.toSun()
				).send({ feeLimit: '50'.toSun() }).then(hex => {
                    sessionStorage.setItem('transactionHex', hex);

                    const interval = setInterval(() => {
                        window.tronWeb.trx.getTransaction(hex).then(transaction => {
                            if (transaction.ret && transaction.ret[0].contractRet === 'SUCCESS') {
                                sessionStorage.removeItem('transactionHex');
                                clearInterval(interval);

                                this.$tronWeb.call({
                                    contractAddress: this.$contracts.ERC20Token.address,
                                    method: 'allowance(address,address)',
                                    parameter: [
                                        { type: "address", value: this.$address },
                                        { type: "address", value: this.$contracts.Manager.address }
                                    ]
                                }).then(result => {
                                    Toast.clear();
                                    this.authorizedAccess = false;
                                    this.authorizationEnds = true;
                                    this.USDTAuthorizedAccess = result.toString().fromSun();
                                    
                                    this.buyGrade();
                                });
                            }
                        })
                    }, 2000);

				}).catch(err => {
					Toast.clear();
					console.log(err);
				});
            },
            end(){
				this.authorizedAccess = false;
			},
            init() {
                //查询等级
				this.$tronWeb.call({
					contractAddress: this.$contracts.Relations.address,
                    method: 'levelOf(address)',
                    parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					const origin = result.constant_result[0].decode(['uint256']);
					// 原等级
                    this.origin = origin.toNumber();
                    this.grade = this.origin < 3 ? this.origin + 1 : this.origin;
                })

                //购买V1需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 1 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);

                    this.v1price = price.toString().fromSun();
                    this.v1Token = sentToken.toString().fromSun();
                })

                //购买V2需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 2 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);
                    
                    this.v2price = price.toString().fromSun();
                    this.v2Token = sentToken.toString().fromSun();
                })

                //购买V3需要的USDT
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
                    method: 'goodsMapping(uint256)',
                    parameter: [{ type: 'uint256', value: 3 }]
				}).then(result => {
                    const { price, sentToken } = result.constant_result[0].decode(['uint256', 'uint256', 'uint256'], ['levelNum', 'price', 'sentToken']);

                    this.v3price = price.toString().fromSun();
                    this.v3Token = sentToken.toString().fromSun();
                })

                // usdt的额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'balanceOf(address)',
					parameter: [{ type: "address", value: this.$address }]
				}).then(result => {
					const res = result.constant_result[0].decode(['uint256']);
					this.usdt = res.toString().fromSun();
                })
                
                // 授权额度
				this.$tronWeb.call({
					contractAddress: this.$contracts.ERC20Token.address,
					method: 'allowance(address,address)',
					parameter: [
						{ type: "address", value: this.$address },
						{ type: "address", value: this.$contracts.Manager.address }
					]
				}).then(result => {
					const res = result.constant_result[0].decode(['uint256']);
					this.USDTAuthorizedAccess = res.toString().fromSun();
                })
                
                // 已购买数量
                this.$tronWeb.call({
					contractAddress: this.$contracts.Manager.address,
					method: 'stockInfoList()'
				}).then(res => {
                    this.boughts = res.constant_result[0].decode(['uint256[]']).map(item => {
                        return item.toString();
                    })
                })
            }
        },
        created() {
            this.$tronWeb && this.init();
        }
    }
</script>

<style lang="scss" scoped>
    .upgrade {
        border-radius: 20px;
        margin: 44px 0 62px;
        padding: 46px 22px 18px;
        border: 2px solid #FFB600;
        box-shadow: 0 2px 4px rgba(#000, .3);
        background-color: #fff;
        &>button {
            width: 100%;
            height: 90px;
            color: #B56900 !important;
            font-size: 44px;
            border-radius: 8px;
            box-shadow: 0 6px 12px rgba(#000, .16);
            margin-top: 10px;
        }
        &>.text {
            color: #656363;
            font-size: 32px;
            text-align: left;
            &>p {
                margin-bottom: 14px;
                &:first-child>span {
                    color: #F9C061;
                }
                &:nth-child(2)>span {
                    color: #21C17A;
                }
            }
        }
        &>.grade {
            display: flex;
            align-items: center;
            // justify-content: space-between;
            justify-content: space-around;
            &>button {
                width: 160px;
                height: 80px;
                border: 2px solid;
                border-color: #CECECE !important;
                border-radius: 8px;
                color: #666666 !important;
                font-size: 44px;
                background-color: transparent;
                margin: 34px 0 30px;
                &.active {
                    border-color: #FFC533 !important;
                    background-color: #FCF5D7;
                }
            }
        }
        &>.title {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #656363;
            font-size: 40px;
            &>p>span {
                color: #F9C061;
            }
        }
    }
</style>